export default function CallExpression(objectExpression) {
    return {
        "type": "CallExpression",
        "callee": {
            "type": "FunctionExpression",
            "id": null,
            "params": [
                {
                    "type": "Identifier",
                    "name": "self"
                }
            ],
            "defaults": [],
            "body": {
                "type": "BlockStatement",
                "body": [
                    {
                        "type": "ReturnStatement",
                        "argument": objectExpression
                    }
                ]
            },
            "rest": null,
            "generator": false,
            "expression": false
        },
        "arguments": [
            {
                "type": "ObjectExpression",
                "properties": []
            }
        ]
    };
}